.banner {
  position: relative;
  min-height: 840px;
  margin-bottom: 57px;
  padding-top: 56px;
  @include respondTo(mobile-max) {
    padding-top: 0;
    min-height: 750px; }
  @include respondTo(tablet) {
    min-height: 610px; }
  @include respondTo(wide) {
    min-height: 790px; }

  &__img {
    opacity: 0;
    z-index: 0;
    position: absolute;

    &-bg {
      top: 0;
      left: 0;
      opacity: 0;
      width: 100%;
      height: 100%;
      max-height: none;
      max-width: none;
      min-height: 100%;
      min-width: 100%;
      transform: translate(-50%, -50%);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      background-attachment: fixed;

      .is-active & {
        opacity: 1; }

      #scene-basketball & {
        background-image: url("../img/img-basketball__banner-bg.png"); }
      #scene-volleyball & {
        background-image: url("../img/img-volleyball__banner-bg.png"); }
      #scene-rugby & {
        background-image: url("../img/img-rugby__banner-bg.png"); }
      #scene-football & {
        background-image: url("../img/img-football__banner-bg.png"); }
      #scene-hockey & {
        background-image: url("../img/img-hockey__banner-bg.png"); } }

    &-person {
      max-width: 100%;
      max-height: 100%;
      @include respondTo(small-mobile) {
        display: none !important; }

      #scene-basketball & {
        left: 30% !important;
        top: auto !important;
        bottom: -15% !important; }
      #scene-volleyball & {
        left: 35% !important;
        top: auto !important;
        bottom: -15% !important; }
      #scene-rugby & {
        left: 47% !important;
        top: auto !important;
        bottom: -10% !important; }
      #scene-football & {
        left: 30% !important;
        top: auto !important;
        bottom: 0% !important; }
      #scene-hockey & {
        left: 53% !important;
        top: auto !important;
        bottom: 2% !important; } }

    &.is-active {
      opacity: 1; }

    &-row {
      z-index: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; }

    &-wrap {
      overflow: hidden;
      z-index: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%; } }

  .container {
    position: relative;
    z-index: 1; }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 70px;
    @include respondTo(tablet) {
      padding-bottom: 40px; } }

  &__row {
    display: flex;
    align-items: center;
    padding-top: 15px;

    &-logo {
      padding-right: 30px;
      @include respondTo(mobile-max) {
        display: none; }
      @include respondTo(tablet) {
        padding-right: 15px; }

      .logo {
        img {
          @include respondTo(mobile-max) {
            z-index: 0;
            position: absolute;
            top: 15px;
            left: 0;
            right: 0;
            margin-left: auto;
            margin-right: auto; } } } }

    &-slogan {
      font-size: 20px;
      color: rgba($colorBlack, 0.7);
      @include respondTo(mobile-max) {
        display: none; }
      @include respondTo(tablet) {
        font-size: 14px; } }

    &-phone {
      font-size: 36px;
      font-weight: 500;
      @include respondTo(mobile-max) {
        display: none; }
      @include respondTo(tablet) {
        font-size: 22px; } }

    &-button {
      padding-left: 65px;
      @include respondTo(mobile-max) {
        padding-left: 0; }
      @include respondTo(tablet) {
        padding-left: 30px; }

      a {
        display: inline-block;
        padding: 12px 25px;
        background-color: $colorBirdFlower;
        color: $colorWhite;
        text-align: center;
        font-size: 20px;
        font-weight: 500;
        @include transition(background-color);
        @include respondTo(mobile-max) {
          padding-left: 15px;
          padding-right: 15px;
          font-size: 16px; }
        @include respondTo(tablet) {
          font-size: 18px; }

        &:hover {
          background-color: $colorBirdFlowerHover;
          @include transition(background-color); } } } }

  &--second {
    .banner__img {
      &-fog {
        display: inline-block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: -100px;
        z-index: 2;
        background-image: url("../img/fog2.png");
        background-position: center bottom;
        background-size: cover; }

      &-bg {
        background-attachment: local;
        // min-width: 1px
        // min-height: 1px
        // width: 100%
        // height: auto
        transform: translate(0,0);
        background-position: center bottom;
 } }        // background-size: contain
    #scene-basketball .banner__img {
      background-image: url("../img/img-basketball_bg.png"); }
    #scene-volleyball .banner__img {
      background-image: url("../img/img-volleyball_bg.png"); }
    #scene-rugby .banner__img {
      background-image: url("../img/img-rugby_bg.png"); }
    #scene-football .banner__img {
      background-image: url("../img/img-football_bg.png"); }
    #scene-hockey .banner__img {
      background-image: url("../img/img-hockey_bg.png"); } }


  &__wrap {
    > h1 {
      margin-bottom: 23px;
      font-size: 60px;
      font-weight: 800;
      line-height: 1.2;
      color: $colorBlack;
      @include respondTo(small-mobile) {
        font-size: 30px; }
      @include respondTo(large-mobile) {
        font-size: 50px; }
      @include respondTo(tablet) {
        margin-bottom: 25px;
        font-size: 35px; }
      @include respondTo(wide) {
        font-size: 50px; }

      span {
        color: $colorBirdFlower; } }

    > h2 {
      padding-left: 50px;
      padding-bottom: 12px;
      font-size: 30px;
      font-family: "KazmannSans", Arial, sans-serif;
      font-weight: normal;
      color: $colorDarkGoldenrod;
      @include respondTo(mobile-max) {
        padding-left: 0; }
      @include respondTo(tablet) {
        padding-left: 30px;
        padding-bottom: 10px;
        font-size: 23px; } }

    > p {
      padding-left: 50px;
      padding-bottom: 38px;
      font-size: 20px;
      @include respondTo(mobile-max) {
        padding-left: 0; }
      @include respondTo(tablet) {
        padding-left: 30px;
        padding-bottom: 25px;
        font-size: 16px; } } }

  &__footer {
    position: relative;
    @include respondTo(mobile-max) {
      display: flex;
      flex-direction: column; } }

  &__box {
    position: absolute;
    left: 0;
    top: 0;
    max-width: 415px;
    padding: 40px 48px;
    background-color: $colorBlack;
    @include respondTo(mobile-max) {
      order: 1;
      position: relative; }
    @include respondTo(tablet) {
      max-width: 315px;
      padding: 25px; }

    p {
      padding-bottom: 23px;
      font-size: 30px;
      font-weight: 500;
      color: $colorWhite;
      @include respondTo(tablet-max) {
        padding-bottom: 20px;
        font-size: 22px; } }

    a {
      display: inline-block;
      padding: 12px 25px;
      background-color: $colorBirdFlower;
      color: $colorWhite;
      text-align: center;
      font-size: 20px;
      font-weight: 500;
      letter-spacing: 0.5px;
      @include transition(background-color);
      @include respondTo(tablet-max) {
        font-size: 18px; }

      &:hover {
        background-color: $colorBirdFlowerHover;
        @include transition(background-color); } } }

  &__pagination {
    order: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 500px;
    margin-left: (415px + 80px);
    padding-top: 70px;
    border-bottom: 1px solid rgba($colorWhite, 0.2);
    @include respondTo(mobile-max) {
      padding-top: 30px;
      margin-left: 0;
      margin-bottom: 15px; }
    @include respondTo(tablet) {
      max-width: 350px;
      margin-left: (315px + 30px);
      padding-top: 50px; }
    @include respondTo(wide) {
      margin-left: (415px + 50px); }

    &-wrap {
      a {
        display: block;
        padding-top: 15px;
        padding-bottom: 10px;
        font-size: 20px;
        font-weight: 500;
        color: $colorWhite;
        border-top: 7px solid transparent;
        border-bottom: 7px solid transparent;
        @include transition(border-color, color);
        @include respondTo(tablet) {
          font-size: 16px; }

        &:hover {
          color: rgba($colorWhiteSmoke, 0.75);
          border-bottom-color: rgba($colorWhiteSmoke, 0.75);
          @include transition(border-color, color); }

        &.is-active {
          border-bottom-color: $colorBirdFlower;
          @include transition(border-color); } } } } }
