.instagram {
  margin-bottom: 50px;

  .main__title {
    padding-bottom: 110px;
    @include respondTo(wide-max) {
      padding-bottom: 50px; }
    @include respondTo(tablet) {
      padding-bottom: 80px; } }


  &__row {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include respondTo(wide-max) {
      justify-content: center; } }


  &__block {
    overflow: hidden;
    cursor: pointer;
    position: relative;
    width: 293px;
    height: 293px;
    margin-bottom: 45px;
    background-color: $colorWhite;
    @include respondTo(wide) {
      margin: 10px; }
    @include respondTo(tablet-max) {
      margin: 15px; }
    @include respondTo(large-mobile) {
      margin: 5px; }
    @include respondTo(small-mobile) {
      margin: 0 0 15px 0; }

    &:hover {
      img {
        transform: scale(1.025);
        @include transition(transform); } }


    img {
      @include transition(transform); }

    //&-btn
    //  position: absolute
    //  top: 10px
    //  right: 10px
    //
    //  i
 } }    //    display: block
