.info {
  overflow: hidden;
  position: relative;
  min-height: 900px;
  margin-top: 25px;
  margin-bottom: 53px;
  @include respondTo(small-mobile) {
    margin-bottom: 0; }

  .container {
    position: relative;
    padding-top: 80px; }

  &__img {
    &-bg {
      position: absolute;
      top: 20px;
      right: 60px;
      @include respondTo(mobile-max) {
        right: auto;
        left: auto;
        margin-left: auto;
        margin-right: auto; }
      @include respondTo(tablet) {
        right: -30%; }
      @include respondTo(wide) {
        right: -10%; } }
    &-forms {
      pointer-events: none;
      user-select: none;
      z-index: 1;
      position: absolute;
      top: 0;
      right: -30px;
      @include respondTo(small-mobile) {
        display: none; }
      @include respondTo(large-mobile) {
        right: -180px; }
      @include respondTo(tablet) {
        right: -30%; }
      @include respondTo(wide) {
        right: -160px; } } }

  &__wrap {
    padding-left: 90px;
    padding-top: 10px;
    @include respondTo(wide-max) {
      padding-left: 0; }

    h1 {
      margin-bottom: 35px;
      color: $colorBlack;
      font-size: 60px;
      font-weight: 800;
      @include respondTo(mobile-max) {
        margin-bottom: 15px;
        font-size: 35px; }
      @include respondTo(tablet) {
        margin-bottom: 25px;
        font-size: 45px; } }

    h2 {
      margin-bottom: 70px;
      font: {
        size: 35px;
        family: "KazmannSans", Arial, sans-serif;
        weight: normal; }
      color: $colorBirdFlower;
      @include respondTo(small-mobile) {
        width: 100%;
        font-size: 23px; }
      @include respondTo(large-mobile) {
        font-size: 25px;
        width: 50%; }
      @include respondTo(tablet) {
        margin-bottom: 55px;
        padding-right: 150px;
        font-size: 30px; } } }

  &__row {
    margin-bottom: 65px;
    @include respondTo(tablet) {
      margin-bottom: 45px; } }

  &__block {
    display: flex;
    @include respondTo(mobile-max) {
      flex-direction: column; }

    &-left {
      width: 435px;
      padding: 33px 45px;
      background-color: $colorWhite;
      @include respondTo(small-mobile) {
        width: 100%; }
      @include respondTo(large-mobile) {
        width: 430px; }
      @include respondTo(tablet) {
        width: 335px;
        padding: 25px; }

      p {
        color: $colorBlack;
        &:first-child {
          font-size: 30px;
          font-weight: 500;
          @include respondTo(tablet-max) {
            font-size: 25px; } }
        &:last-child {
          font-size: 18px;
          @include respondTo(tablet-max) {
            font-size: 16px; } }

        span {
          color: $colorBirdFlower; } } }

    &-right {
      width: 415px;
      padding: 40px 45px;
      background-color: $colorBlack;
      @include respondTo(small-mobile) {
        width: 100%; }
      @include respondTo(large-mobile) {
        width: 430px; }
      @include respondTo(tablet) {
        width: 315px;
        padding: 25px; }

      p {
        margin-bottom: 25px;
        font-size: 30px;
        font-weight: 500;
        color: $colorWhite;
        @include respondTo(tablet-max) {
          font-size: 25px; } }

      a {
        display: inline-block;
        padding: 12px 25px;
        background-color: $colorBirdFlower;
        color: $colorWhite;
        text-align: center;
        font-size: 20px;
        font-weight: 500;
        @include transition(background-color);
        @include respondTo(tablet-max) {
          font-size: 18px; }

        &:hover {
          background-color: $colorBirdFlowerHover;
          @include transition(background-color); } } } }

  &__list {
    margin-right: 30%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    &-iconWrap {
      width: 45px; }

    &-wrap {
      display: flex;
      align-items: center;
      min-width: 360px;
      margin-bottom: 15px;
      @include respondTo(mobile-max) {
        min-width: 100%; }

      .ico {
        fill: $colorBirdFlower;

        &-info {
          &-0 {
            width: 33px;
            height: 33px; }
          &-1 {
            width: 29px;
            height: 34px; }
          &-2 {
            width: 27px;
            height: 26px; }
          &-3 {
            width: 34px;
            height: 28px; }
          &-4 {
            width: 34px;
            height: 31px; }
          &-5 {
            width: 33px;
            height: 32px; } } }

      p {
        margin-left: 10px;
        font-size: 18px;
        @include respondTo(tablet-max) {
          font-size: 16px; } } } } }

