/* HEADER */
.header {
  z-index: 10;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  @include transition(background-color);
  @include respondTo(mobile-max) {
    display: none; }

  &.is-fixed {
    background-color: $colorWhiteSmoke;
    @include transition(background-color); }

  &__hamburger {
    display: block;
    @include respondTo(tablet-min) {
      display: none; } }

  &__wrapper {
    @include respondTo(mobile-max) {
      opacity: 0;
      visibility: hidden; } } }
