.work {
  &__wrap {
    padding-top: 83px;
    padding-bottom: 117px;
    background-image: url("../img/img-bg__work.png");
    background-repeat: repeat;
    @include respondTo(tablet) {
      padding-top: 63px;
      padding-bottom: 97px; }
    @include respondTo(mobile-max) {
      padding-top: 50px;
      padding-bottom: 80px; } }



  .main__subtitle {
    color: $colorWhite; }

  .main__title {
    padding-bottom: 45px; }

  &__row {
    display: flex;
    justify-content: center;
    @include respondTo(small-mobile) {
      flex-direction: column;
      align-items: center; }
    @include respondTo(large-mobile) {
      flex-wrap: wrap; }
    @include respondTo(tablet) {
      flex-wrap: wrap; } }

  &__block {
    @include respondTo(tablet-max) {
      margin-bottom: 30px; }

    &:hover {
      .work__circle {
        border-color: $colorWhite;
        @include transition(border-color);

        .ico {
          fill: $colorBlack;
          @include transition(fill); }
        span {
          background-color: $colorWhite;
          color: $colorBlack;
          @include transition(background-color, color); } }

      .work__block-text {
        color: $colorWhite;
        @include transition(color); } }

    > p {
      text-align: center;
      font: {
        size: 20px;
        weight: 500; }
      @include transition(color); } }

  &__circle {
    cursor: pointer;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 160px;
    height: 160px;
    margin: {
      bottom: 20px;
      left: 23px;
      right: 23px; }
    border-radius: 100%;
    border: 2px solid rgba($colorWhite, 0.2);
    @include transition(border-color);
    @include respondTo(tablet) {
      width: 140px;
      height: 140px; }
    @include respondTo(wide) {
      width: 150px;
      height: 150px; }

    .ico {
      fill: $colorWhite;
      @include transition(fill);
      &-work {
        &-0 {
          width: 71px;
          height: 70px; }
        &-1 {
          width: 52px;
          height: 109px; }
        &-2 {
          width: 91px;
          height: 82px; }
        &-3 {
          width: 58px;
          height: 83px; }
        &-4 {
          width: 81px;
          height: 81px; } } }

    span {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 25px;
      right: -4px;
      width: 32px;
      height: 32px;
      border-radius: 100%;
      background-color: $colorNero;
      color: $colorWhite;
      font: {
        size: 25px;
        family: "KazmannSans", Arial, sans-serif; }
      @include transition(background-color, color); } } }
