.modal {
  position: relative;
  max-width: 415px;
  min-height: 300px;
  margin: 40px auto;
  padding: 45px;
  background-color: $colorWhite;
  &--wide {
    max-width: 1200px; }

  .show & {
    animation-duration: 0.5s;
    animation-name: fadeIn; }

  &__form {

    &-field {
      &--hidden {
        cursor: pointer;
        position: relative;
        margin-bottom: 20px;

        * {
          cursor: pointer; }

        input {
          opacity: 0;
          z-index: -1;
          overflow: hidden;
          position: absolute;
          width: 0.1px;
          height: 0.1px;

          + label {
            display: inline-block;
            font-size: 12px;
            color: $colorBirdFlower;

            p {
              font-size: 12px;
              text-decoration: underline;
              @include transition(color);
              &:hover {
                color: $colorBirdFlowerHover;
                @include transition(color); } }

            span {
              display: block;
              color: $colorBlack;
              font-weight: 300;
              margin-top: 5px; } } } } }

    h2 {
      font-size: 30px;
      font-weight: 500;
      padding-bottom: 45px; }
    h1, h3, h4, h5 {
      font-weight: 500;
      margin: 1em 0; }
    h3 {
      font-size: 24px; }
    h4 {
      font-size: 20px; }
    p {
      font-size: 16px;
      line-height: 1.4; }
    ul {
      margin: 1em 0;
      padding: 0;
      li {
        display: block;
        margin-bottom: .5em;
        position: relative;
        font-size: 16px;
        padding-left: 20px;
        &::before {
          display: inline-block;
          content: ' ';
          size: 5px;
          border-radius: 50%;
          background: currentColor;
          position: absolute;
          left: 0;
          top: 7px; } } }

    button {
      margin-top: 30px;
      padding: 12px 25px;
      border: 0;
      background-color: $colorBirdFlower;
      color: $colorWhite;
      text-align: center;
      font-family: inherit;
      font-size: 20px;
      font-weight: 500; }

    input {
      width: 100%;
      height: 50px;
      padding-left: 15px;
      margin-bottom: 10px;
      border: none;
      background-color: $colorGainsboro;
      font-size: 16px;
      font-family: inherit;
      &::-webkit-input-placeholder {
        font-size: 16px; }
      &::-moz-placeholder {
        font-size: 16px; }
      &:-ms-input-placeholder {
        font-size: 16px; }
      &:-moz-placeholder {
        font-size: 16px; } }

    .ui-checkbox {
      margin-top: 10px; } } }

.error {
  font-size: 12px;
  color: red !important;
  margin-bottom: 5px; }

.show.mfp-bg {
  background-color: rgba($colorBlack, 0.7);
  transition: background-color 0.5s ease-in-out;

