.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &__mobile {
    display: none;
    z-index: 100;
    overflow-y: auto;
    overflow-x: hidden;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    background-color: rgba($colorBlack, 0.55);

    &.is-active {
      display: block; }

    &-close {
      z-index: 10;
      position: absolute;
      top: 5px;
      right: 55px;

      .hamburger-inner {
        background-color: $colorWhite;
        &::before, &::after {
          background-color: $colorWhite; } } }

    .menu {
      position: absolute;
      top: 0;
      width: calc(100% - 50px);
      height: 100%;
      background-color: #000;
      color: $colorWhite;
      flex-direction: column;
      justify-content: center;

      &__item {
        height: 55px;
        line-height: 55px; } } }


  .footer & {
    height: 62px;
    border-top: 1px solid $colorGainsboro;
    border-bottom: 1px solid $colorGainsboro;
    @include respondTo(tablet-max) {
      display: none; }
    @include respondTo(tablet) {
      height: 50px; } }

  .header & {
    height: 56px;
    background-color: transparent;
    border-bottom: 1px solid $colorGainsboro;
    @include respondTo(tablet) {
      height: 50px; } }

  &__item {
    display: flex;
    align-items: center;
    height: 100%;
    font-size: 18px;
    font-weight: 300;
    border-top: 4px solid transparent;
    border-bottom: 4px solid transparent;
    @include transition(border-color);
    @include respondTo(tablet) {
      font-size: 14px; }
    @include respondTo(wide) {
      font-size: 16px; }

    &:hover {
      border-bottom-color: $colorBirdFlower;
      @include transition(border-color); } } }
