.social {
  display: flex;
  align-items: center;
  @include respondTo(tablet-max) {
    justify-content: center; }

  &__item {
    svg {
      fill: $colorBirdFlower;
      display: inline-block;
      vertical-align: middle;
      margin-right: 20px;
      font-size: 20px;
      @include transition(fill);

      &:hover {
        fill: $colorDarkGoldenrod;
        @include transition(fill); } }

    &:last-child {
      svg {
        margin-right: 0; } } } }
